import axios from 'axios';

const GET_COLLECTION_NAMES_AND_IDS_URL = `${process.env.REACT_APP_API_URL}/get-all-collection-name-and-id.php`;
const GET_MUSICIAN_META_URL = `${process.env.REACT_APP_API_URL}/get-musician-meta-for-admin.php`;
const UPDATE_MUSICIAN_META_URL = `${process.env.REACT_APP_API_URL}/update-musician-meta.php`;

export function getCollectionNamesAndID() {
    return axios({
      method: 'get',
      url: GET_COLLECTION_NAMES_AND_IDS_URL,
      headers: {
        'content-type': 'application/x-www-form-urlencoded',
      }
    })
}

export function getMusicianMeta() {
    return axios({
      method: 'get',
      url: GET_MUSICIAN_META_URL,
      headers: {
        'content-type': 'application/x-www-form-urlencoded',
      }
    })
}

export function updateMusicianMeta(
  bannerVideoURL: string,
  musicianVideoURL: string,
  youtubeVideoURL: string,
  artistID: string,
  collectionID: string,
  musicianDescription: string
) {
  return axios({
    method: 'post',
    url: UPDATE_MUSICIAN_META_URL,
    headers: {
      'content-type': 'application/x-www-form-urlencoded',
    },
    data: {
      bannerVideoURL,
      musicianVideoURL,
      youtubeVideoURL,
      artistID,
      collectionID,
      musicianDescription
    }
  })
}