import { useEffect, useState } from "react"
import { FormattedMessage, useIntl } from "react-intl"
import { shallowEqual, useSelector } from "react-redux"
import CircularProgress from "@mui/material/CircularProgress"
import { RootState } from "setup"
import { AlertDanger, AlertSuccess } from "../alerts/Alerts"
import { getArtistsData } from "../artistWizard/api"
import { getMusicianMeta, updateMusicianMeta } from "./api"

export const MusicianMetadataManagement = () => {
    const [loading, setLoading] = useState<boolean>(false)
    const [fetchingData, setFetchingData] = useState<boolean>(true)
    const [successMessage, setSuccessMessage] = useState<string>("")
    const [errorMessage, setErrorMessage] = useState<string>("")
    const [checked, setChecked] = useState<boolean>(false)

    const [artistData, setArtistData] = useState<Array<any>>([])
    const [collectionData, setCollectionData] = useState<Array<any>>([])
    const [bannerVideoURL, setBannerVideoURL] = useState<string>("")
    const [musicianDescription, setMusicianDescription] = useState<string>("")
    const [musicianVideoURL, setmusicianVideoURL] = useState<string>("")
    const [youtubeVideoURL, setYoutubeVideoURL] = useState<string>("")
    const [selectedArtist, setSelectedArtist] = useState<string>("")
    const [selectedCollection, setSelectedCollection] = useState<string>("")

    const intl = useIntl()

    const { isAdmin }: any = useSelector<RootState>(
        ({auth}) => ({
            isAdmin: auth.user?.isAdmin
        }), shallowEqual
    )

    useEffect(() => {
        getMusicianMeta()
         .then(async (response) => {
            setArtistData(response.data.artist_data)
            setCollectionData(response.data.collectionData)
            setBannerVideoURL(response.data.banner_video_url)
            setmusicianVideoURL(response.data.musician_video_url)
            setSelectedCollection(response.data.featured_collection)
            setSelectedArtist(response.data.featured_artist)
            setYoutubeVideoURL(response.data.youtube_video_url)

            let musicianDescriptArr = JSON.parse(response.data.musician_description.replace(/&quot;/g, '"'))
            let musicianDescriptStr = ""

            await musicianDescriptArr.map((lines : any) => {
                musicianDescriptStr += lines + '\n'
            })

            setMusicianDescription(musicianDescriptStr)
            setFetchingData(false)
         })
    }, [])

    if(successMessage !== "") {
        setTimeout(() => {
          setChecked(false);
          setTimeout(() => {
            setSuccessMessage("");
          }, 200);
        }, 5000);
    }
    
    if(errorMessage !== "") {
        setTimeout(() => {
            setChecked(false);
            setTimeout(() => {
            setErrorMessage("");
            }, 200);
        }, 5000);
    }

    const handleBannerVideoURLChange = (event: any) => {
        event.preventDefault()

        setBannerVideoURL(event.target.value)
    }

    const handleMusicianVideoURLChange = (event: any) => {
        event.preventDefault()

        setmusicianVideoURL(event.target.value)
    }

    const handleYoutubeVideoURLChange = (event: any) => {
        event.preventDefault()

        setYoutubeVideoURL(event.target.value)
    }

    const handleMusicianDescriptionChange = (event: any) => {
        event.preventDefault()

        setMusicianDescription(event.target.value)
    }

    const handleCollectionChange = (event: any) => {
        event.preventDefault()

        setSelectedCollection(event.target.value)
    }

    const handleArtistChange = (event: any) => {
        event.preventDefault()

        setSelectedArtist(event.target.value)
    }

    const handleSubmit = () => {
        setLoading(true)

        let musicianDescArr = musicianDescription.split('\n')

        updateMusicianMeta(
            bannerVideoURL,
            musicianVideoURL,
            youtubeVideoURL,
            selectedArtist,
            selectedCollection,
            JSON.stringify(musicianDescArr)
        )
        .then((response) => {
            if(response.data.success) {
                setChecked(true)
                setSuccessMessage(response.data.message)
                window.scrollTo(0,0)
                setLoading(false)
            } else {
                setChecked(true)
                setSuccessMessage(intl.formatMessage({ id: 'MUSICIAN_META.UPDATE_FAILED' }))
                window.scrollTo(0,0)
                setLoading(false)
            }
        })
        .catch(() => {
            setChecked(true)
            setSuccessMessage(intl.formatMessage({ id: 'MUSICIAN_META.UPDATE_FAILED' }))
            window.scrollTo(0,0)
            setLoading(false)
        })
    }

    return (
        <>
            {isAdmin == '1' &&
                <>
                {successMessage !== "" ? (
                    <AlertSuccess message={successMessage} checked={checked} />
                ) : null }
        
                {errorMessage !== "" ? (
                    <AlertDanger message={errorMessage} checked={checked} />
                ) : null }
        
                {!fetchingData && 
                    <>
                        <div className='card'>
                            <div className='card-body'>
                
                                <div
                                    className='stepper stepper-links d-flex flex-column pt-15'
                                    id='kt_create_account_stepper'
                                >
                                    <div className='stepper-nav mb-5'>
                                        <div className='stepper-item current'>
                                            <h3 className='stepper-title'><FormattedMessage id="MUSICIAN_META.TITLE" /></h3>
                                        </div>
                                    </div>
                                </div>
                                <div className='mx-auto mw-600px w-100 pt-15 pb-10'>
                                    <div className='w-100'>
                                        
                                        <div className='fv-row mb-10'>
                                            <label className='form-label required'>
                                            <FormattedMessage id="MUSICIAN_META.FEATURED_COLLECTION" />
                                            </label>

                                            <select
                                                value={selectedCollection}
                                                className='form-select form-select-lg form-select-solid'
                                                name='artistName'
                                                onChange={(e) => handleCollectionChange(e)}
                                            >
                                                <option value="">
                                                    {/* <FormattedMessage id="MUSICIAN_META.SELECT_COLLECTION" /> */}
                                                    {intl.formatMessage({ id: 'MUSICIAN_META.SELECT_COLLECTION' })}
                                                </option>

                                                {collectionData.map((data: any) => (
                                                    <option key={data.id} value={data.id}>{data.collection_name}</option>
                                                ))}
                                            </select>
                                        </div>
                                        
                                        <div className='fv-row mb-10'>
                                            <label className='form-label required'>
                                                <FormattedMessage id="MUSICIAN_META.FEATURED_ARTIST" />
                                            </label>

                                            <select
                                                value={selectedArtist}
                                                className='form-select form-select-lg form-select-solid'
                                                name='artistName'
                                                onChange={(e) => handleArtistChange(e)}
                                            >
                                                {/* <option value=''><FormattedMessage id="MUSICIAN_META.SELECT_ARTIST" /></option> */}
                                                <option value="">{intl.formatMessage({ id: 'MUSICIAN_META.SELECT_ARTIST' })}</option>

                                                {artistData.map((data: any) => (
                                                    <option key={data.id} value={data.id}>{data.artist_name}</option>
                                                ))}
                                            </select>
                                        </div>
                                        
                                        <div className='mb-10 fv-row'>
                                            <label className='form-label mb-3'>
                                                {/* <FormattedMessage id="CREATE_EDIT_ARTIST.ARTIST_IMAGE" />
                                                <span className='ms-1'>(Recommended size: 500 x 500)</span> */}
                                                <FormattedMessage id="MUSICIAN_META.BANNER_VIDEO_URL" />
                                            </label>
                
                                            <input
                                                type='text'
                                                className='form-control form-control-lg form-control-solid'
                                                name='artistName'
                                                placeholder=''
                                                value={bannerVideoURL}
                                                onChange={(event) => handleBannerVideoURLChange(event)}
                                            />
                                        </div>  
                
                                        <div className='mb-10 fv-row'>
                                            <label className='form-label mb-3'>
                                                {/* <FormattedMessage id="CREATE_EDIT_ARTIST.ARTIST_COVER_IMAGE" />
                                                <span className='ms-1'>(Recommended size: 1500 x 300)</span> */}
                                                <FormattedMessage id="MUSICIAN_META.MUSICIAN_VIDEO_URL" />
                                            </label>
                
                                            <input
                                                type='text'
                                                className='form-control form-control-lg form-control-solid'
                                                name='artistName'
                                                placeholder=''
                                                value={musicianVideoURL}
                                                onChange={(event) => handleMusicianVideoURLChange(event)}
                                            />
                                        </div>  

                                        <div className='mb-10 fv-row'>
                                            <label className='form-label mb-3'>
                                                {/* <FormattedMessage id="CREATE_EDIT_ARTIST.ARTIST_COVER_IMAGE" />
                                                <span className='ms-1'>(Recommended size: 1500 x 300)</span> */}
                                                <FormattedMessage id="MUSICIAN_META.YOUTUBE_VIDEO_URL" />
                                            </label>
                
                                            <input
                                                type='text'
                                                className='form-control form-control-lg form-control-solid'
                                                name='artistName'
                                                placeholder=''
                                                value={youtubeVideoURL}
                                                onChange={(event) => handleYoutubeVideoURLChange(event)}
                                            />
                                        </div>
                
                                        <div className='mb-10 fv-row'>
                                            <label className='form-label mb-3 required'>
                                                {/* <FormattedMessage id="CREATE_EDIT_ARTIST.ABOUT_ARTIST" /> */}
                                                <FormattedMessage id="MUSICIAN_META.PLATFORM_DESCRIPTION" />
                                            </label>
                
                                            <textarea
                                                rows={5}
                                                className='form-control form-control-lg form-control-solid'
                                                value={musicianDescription}
                                                onChange={(event) => handleMusicianDescriptionChange(event)}
                                                // onKeyPress={(event) => handleEnterPress(event)}
                                            />
                                        </div>
                                        </div>
                                        <div className='d-flex flex-stack pt-15'>
                                        <div className='mr-2'>
                                        </div>
                
                                        <div>
                                            {loading && (
                                                <button type='submit' className='btn btn-lg btn-primary me-3'>
                                                    <FormattedMessage id="CREATE_EDIT_COLLECTION.SUBMITTING" />..
                                                    <span className='spinner-border spinner-border-sm align-middle ms-2'></span>
                                                </button>
                                            )
                                            }
                    
                                            {!loading && (
                                                <button 
                                                    className='btn btn-lg btn-primary me-3'
                                                    onClick={handleSubmit}
                                                >
                                                    <FormattedMessage id="CREATE_EDIT_COLLECTION.SUBMIT" />
                                                </button>
                                            )}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </>
                }
                {fetchingData && 
                    <div className='d-flex w-100 h-100'>
                        <div className='my-auto mx-auto'>
                            <div className='d-flex flex-column justify-content-center'>
                                <span className='text-primary fs-3'><FormattedMessage id="AUTH.REGISTER.LOADING" /></span> <br />
                                <span className='ms-4'>
                                    <CircularProgress />
                                </span>
                            </div>
                        </div>
                    </div>
                }
                </>
            } 
        </>
    )
}